import React, { Fragment } from "react";
import PropTypes from "prop-types";
import { Link } from "react-router-dom";
import Moment from "react-moment";
import { connect } from "react-redux";
import { addYike, removeYike, deletePost } from "../../actions/post";

const PostItem = ({
  auth,
  post: { _id, text, name, avatar, user, yikes, comments, date },
  addYike,
  removeYike,
  deletePost
}) => (
  <div className='post bg-white p-1 my-1'>
    <div>
      <Link to={`/profile/${user}`}>
        <img className='round-img' src={avatar} alt='' />
        <h4>{name}</h4>
      </Link>
    </div>
    <div>
      <p className='my-1'>{text}</p>
      <p className='post-date'>
        Posted on <Moment format='DD/MM/YYYY'>{date}</Moment>
      </p>
      <button
        onClick={e => addYike(_id)}
        type='button'
        className='btn btn-light'
      >
        <i className='fas fa-thumbs-up'></i>{" "}
        <span>{yikes.length > 0 && <span>{yikes.length}</span>}</span>
      </button>
      <button
        onClick={e => removeYike(_id)}
        type='button'
        className='btn btn-light'
      >
        <i className='fas fa-thumbs-down'></i>
      </button>
      <Link to={`/posts/${_id}`} className='btn btn-primary'>
        Discussion{" "}
        {comments.length > 0 && (
          <span className='comment-count'>{comments.length}</span>
        )}
      </Link>
      {!auth.loading && user === auth.user._id && (
        <button
          onClick={e => deletePost(_id)}
          type='button'
          className='btn btn-danger'
        >
          <i className='fas fa-times'></i>
        </button>
      )}
    </div>
  </div>
);

PostItem.defaultProps = {
  showActions: true
};

PostItem.propTypes = {
  post: PropTypes.object.isRequired,
  auth: PropTypes.object.isRequired,

  addYike: PropTypes.func.isRequired,
  removeYike: PropTypes.func.isRequired,
  deletePost: PropTypes.func.isRequired
};
const mapStateToProps = state => ({
  auth: state.auth
});
export default connect(mapStateToProps, { addYike, removeYike, deletePost })(
  PostItem
);
